<template>
  <div class="content-box">
    <!--<common-header :tittle="tittle" :showback="false" ></common-header>-->
    <div class="page-content">
      <li v-for="item in shoplist">
        <div class="top">
          <div>
            <mt-checklist
              v-model="value"
              :options="[item.dealer]"
              class="top-mint-cell"
            >
            </mt-checklist>
          </div>
          <div class="top-right">{{item.inventory}}</div>
        </div>
        <div class="bottom">
          <div class="bottom-left">
            <img :src="item.src" alt="">
          </div>
          <div class="text">
              <p class="text-title">{{item.type}}</p>
            <p>{{item.determine}}</p>
            <p><span>金融方式</span>&nbsp;&nbsp;<span>{{item.financial}}</span></p>
            <p>订单价格<span class="text-price">{{item.price}}</span></p>
          </div>
        </div>

      </li>
      <mt-button class="btn" >车辆对比</mt-button>
      <!--<mt-button @click="todetail">footprint11111</mt-button>-->
    </div>
  </div>
</template>

<script>
import {mapMutations, mapGetters, mapState} from 'vuex'
import commonHeader from 'common/common-header'
import car1 from '../../assets/image/shopcar1.png'
export default {
  data () {
    return {
      tittle: '购物车',
      back:'返回',
      num: 0,
      value:'',
      shoplist:[
        {
          dealer:"XXX经销商",
          inventory:'库存充足',
          src:car1,
          type:'尼欧 || 豪华型 ',
          determine:'2018款/黑色/21度/200KM',
          financial:' 状态',
          price:'5.8万'
        },
        {
          dealer:"XXX经销商",
          inventory:'库存充足',
          src:car1,
          type:'尼欧 || 豪华型 ',
          determine:'2018款/黑色/21度/200KM',
          financial:'   状态',
          price:' 5.8万'
        },
        {
          dealer:"XXX经销商",
          inventory:'库存充足',
          src:car1,
          type:'尼欧 || 豪华型 ',
          determine:'2018款/黑色/21度/200KM',
          financial:'   状态',
          price:' 5.8万'
        },
        {
          dealer:"XXX经销商",
          inventory:'库存充足',
          src:car1,
          type:'尼欧 || 豪华型 ',
          determine:'2018款/黑色/21度/200KM',
          financial:'   状态',
          price:' 5.8万'
        }
      ]
    }
  },
  created() {},
  methods: {
    ...mapMutations({
      setNum: 'SET_NUM'
    }),
    todetail() {
      this.$router.togo('/Home/Detail')
    }
  },
  components: {
    commonHeader
  },
  computed: {
    ...mapGetters([
      'number'
    ]),
    ...mapState({
      number: state => state.home.number
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import "~styles/index.less";
@import "~styles/variable.less";
.page-content{
  .mb(98);
  /*margin-top: 1.2rem;*/
  font-size: @body-font-size;
  margin-top: 1rem;
  .btn{
    background:#FF7414;
    width: 100%;
    color: #fff;
    position: fixed;
    bottom: 1.4rem;
    left: 0;
  }
  li{
    width: 100%;
    height: auto;
    margin-top: 0.2rem;
    background: #fff;
    .top{
      width: 100%;
      /*height:1rem;*/
      display:flex;
      justify-content: space-between;
      align-items: center;
      .top-right{
        width: 2.3rem;
        height: 0.8rem;
        background: #E8FCF3;
        color: #2FD26C;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 0.2rem;
      }
      /*border: 1px solid red;*/
      >div{
        .top-mint-cell{
            min-height: 0px !important;
            .mint-cell-wrapper {
               /*background-image:#fff !important;*/
               /*background-image:  !important;*/
               background-size: 120% 1px;
               background-repeat: no-repeat;
               background-position: top left;
               background-origin: content-box;
               -webkit-box-align: center;
              -ms-flex-align: center;
               align-items: center;
              -webkit-box-sizing: border-box;
               box-sizing: border-box;
               display: -webkit-box;
              display: -ms-flexbox;
               display: flex;
               font-size: 16px;
               line-height: 1;
               min-height: inherit;
               overflow: hidden;
               padding: 0 10px;
               width: 100%;
            }
           /*background-repeat: ; */
          }
      }
    }
    .bottom{
      width: 100%;
      /*height: 2rem;*/
      display: flex;
      justify-content: space-between;
      .bottom-left{
        width: 49%;
      }
      .text{
        width: 49%;
        text-align: left;
        letter-spacing: 0.01rem;
        color: #B1B1B1;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .text-title{
          color: #000 !important;
        }
        .text-price{
          color: red;
          font-weight: 600;
        }
        p{
          /*display: flex;*/
          /*justify-content: space-between;*/
        }
      }
      div{
        /*width: 40%;*/
        /*border: 1px solid red;*/
        img{
          width: 100%;
          display: block;
          height: 100%;
        }
      }
    }
  }
}
</style>



